@mixin tablet {
    @media (max-width: 992px) {
        @content;
    }
}

@mixin pad {
    @media (max-width: 768px) {
        @content;
    }
}

@mixin phoneH {
    @media (max-width: 568px) {
        @content;
    }
}

@mixin phoneV {
    @media (max-width: 375px) {
        @content;
    }
}
